.footer {
  background-color: #202020;
  color: #f4f4f4;
  min-height: 15vh;
}

.footer .text--bold {
  font-family: "Raleway", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #e4e4e4;
}

.footer__section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1500px;
  margin: auto auto;
  padding: 20px 10px;
}

.footer__section > div {
  display: flex;
  margin: auto auto;
  justify-content: space-around;
  padding: 10px;
  text-align: center;
  width: 90%;
}

.footer__p {
  padding: 0;
  margin: 0;
  color: #33eebb;
}

.footer__p--bold {
  font-weight: bold;
}

.footer__section--small p {
  width: 80%;
  padding: 10px;
  text-align: center;
  margin: auto auto;
  border-top: 1px solid #f4f4f4;
}

@media screen and (min-width: 768px) {
  .footer__section {
    flex-direction: row;
    width: 80%;
  }

  .footer__section > div {
    text-align: left;
  }

  .footer__row {
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
  }

  .footer__row > div {
    padding: 5px;
  }
}

@media screen and (min-width: 1920px) {
  .footer__section {
    padding: 20px 30px;
  }
}
